export default {
	data() {
		return {
			headtab:[],
			tab:[]
		};
	},
	created() {
		this.getHeadtab()
	},
	methods: {
		async getHeadtab() {
			const res = await this.myRequest({
				url: '/index/swiper'
			});
			this.headtab=res.data[0].data.headtab
			this.tab=res.data[0].data.tab
		}
	}
};
